import dayjs from 'dayjs';
import Loading from '@/components/Loading';
import Error from '@/components/Error';
// import Comments from '@/components/Comments';
export default {
    name: 'PageTimeline',
    components: {
        Loading,
        Error
        // Comments
    },
    head() {
        return Object.assign(this.$nuxtI18nSeo(), {
            title: `时间轴 - ${process.env.title}`,
            meta: [
                {
                    hid: 'description',
                    name: 'description',
                    content: '文章历史归档'
                }
            ]
        });
    },
    data() {
        return {
            loading: false,
            error: null
        };
    },
    fetch({ store, params, redirect }) {
        if (process.server) {
            store.commit('SET_STORE', {
                key: 'ssr_init',
                value: true
            });
            return store.dispatch('getTimeline');
        }
    },
    mounted() {
        if (this.$store.state.ssr_init) return this.$store.commit('resetInit');
        this.getData();
    },
    methods: {
        getData() {
            this.loading = true;
            this.$store
                .dispatch('getTimeline')
                .then((e) => {
                    this.loading = false;
                })
                .catch((err) => {
                    this.error = err;
                    this.loading = false;
                });
        },
        dayjs
    }
};
